import React, { Suspense } from "react";
import { Routes, Route, Navigate, Link } from "react-router-dom";

import AllQuotes from "./pages/AllQuotes";
// import QuoteDetail from "./pages/QuoteDetail";
// import NewQuote from "./pages/NewQuote";
// import NotFound from "./pages/NotFound";

// import Comments from "./components/comments/Comments";
import Layout from "./components/layout/Layout";
// import LoadingSpinner from "./components/UI/LoadingSpinner";

const NewQuote = React.lazy(() => import("./pages/NewQuote"));
const QuoteDetail = React.lazy(() => import("./pages/QuoteDetail"));
const NotFound = React.lazy(() => import("./pages/NotFound"))

const Comments = React.lazy(() => import("./components/comments/Comments"));
const LoadingSpinner = React.lazy(() => import("./components/UI/LoadingSpinner"));

function App() {
  return (
    <Layout>
      <Suspense
        fallback={
          <div className="centered">
            <LoadingSpinner />
          </div>
        }
      >
        <Routes>
          <Route
            path="/QuotesProject/"
            element={<Navigate to="/QuotesProject/quotes" />}
          />
          <Route path="/QuotesProject/quotes" element={<AllQuotes />} />
          <Route
            path="/QuotesProject/quotes/:quoteId"
            element={<QuoteDetail />}
          >
            <Route
              path=""
              element={
                <div className="centered">
                  <Link className="btn--flat" to={`comments`}>
                    Load Comments
                  </Link>
                </div>
              }
            />
            <Route path={`comments`} element={<Comments />} />
          </Route>
          <Route path="/QuotesProject/new-quote" element={<NewQuote />} />
          <Route path="/QuotesProject/*" element={<NotFound />} />
        </Routes>
      </Suspense>
    </Layout>
  );
}

export default App;
